<template>
  <div class="card">
    <h3 class="card-header">{{ categoryName }}</h3>
    <div class="card-body">
      <p
        style="text-align:left"
        v-html="
          require('markdown-it')().render(foes[categoryName]['Description'])
        "
      ></p>

      <div class="accordion" :id="'accordion' + categoryId">
        <foe
          v-for="(value, propertyName) in foes[categoryName]['Foes']"
          v-bind:key="propertyName"
          :foe-name="propertyName"
          :foe-data="value"
          :category-id="categoryId"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Foe from "./Foe.vue";

export default {
  name: "FoeCategory",
  props: ["categoryName"],
  components: {
    Foe
  },
  computed: {
    categoryId() {
      return this.$props.categoryName.replace(/\s+/g, "").replace(/[()]/g, "");
    },
    foes() {
      return this.$store.state.Foes;
    }
  }
};
</script>
