<template>
  <div>
    <div class="accordion-item">
      <h2 class="accordion-header" :id="'heading' + foeId">
        <button
          class="accordion-button collapsed"
          type="button"
          data-bs-toggle="collapse"
          :data-bs-target="'#collapse' + foeId"
          aria-expanded="false"
          :aria-controls="'collapse' + foeId"
        >
          {{ foeName }}
        </button>
      </h2>
      <div
        :id="'collapse' + foeId"
        class="accordion-collapse collapse"
        :aria-labelledby="'heading' + foeId"
        :data-bs-parent="'#accordion' + categoryId"
        style="text-align: left; padding-top: 1.25rem; padding-left:1rem; padding-right:1rem"
      >
        <p
          style="float: right"
          v-html="'Rank: <strong>' + foeData['Rank'] + '</strong>'"
        ></p>
        <h4>Features</h4>
        <ul style="text-align: left">
          <li v-for="value in foeData['Features']" v-bind:key="value">
            {{ value }}
          </li>
        </ul>
        <h4>Drives</h4>
        <ul style="text-align: left">
          <li v-for="value in foeData['Drives']" v-bind:key="value">
            {{ value }}
          </li>
        </ul>
        <h4>Description</h4>
        <div
          class="accordion-body"
          style="text-align: left"
          v-html="content"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Foe",
  props: {
    foeName: String,
    categoryId: String,
    foeData: Object
  },
  computed: {
    content() {
      var md = require("markdown-it")();
      return md.render(this.$props.foeData["Description"]);
    },
    foeId() {
      return this.$props.foeName.replace(/\s+/g, "").replace(/[()]/g, "");
    }
  }
};
</script>
